import React,{useState,useEffect} from 'react'
import {
  
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow
} from '@coreui/react'
import { Link } from 'react-router-dom';
import API from 'src/api';

const UserList = () => {    
  const fields = ['name','email','delete'];
  const [userData,setUserData] = useState([{}]);
  const [isLoading,setIsLoading] = useState(false);

  useEffect(()=>{
    getData();
  },[]);

  async function getData(){
    try {
      setIsLoading(true);    
      var response = await API.get("/users/list?role=user");  
      setUserData(response.data.data);
      setIsLoading(false);
    } catch (error) {   
      alert("Try Again");   
    }
  }

  const deleteUser = async (id)=>{
    try {      
      setIsLoading(true);    
      var response = await API.delete("/users/"+id);  
      if(response.status === 201)
      {
        var data = userData.filter((a)=>{ return a._id !== id});
        setUserData(data);
      }
      setIsLoading(false);
    } catch (error) {
      alert("api error");
    }
  }

  return (
    <>
      <div className="">
        <CRow className="p-2 ml-auto">
        </CRow>
        <CRow>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">
                User List
              </span>
              <Link to={{pathname:`/adduser/`}} className="btn btn-primary float-right">              
                Add
              </Link>
              {/* <Link to={{pathname:`/topics/${id}`}} className="btn btn-outline-info float-right mx-2">
                Back
              </Link> */}
            </CCardHeader>
            <CCardBody>
            {isLoading ? <div>Loading...</div> : 
            <CDataTable
              items={userData}
              fields={fields}
              itemsPerPage={5}
              loading={isLoading}
              pagination
              scopedSlots = {{              
                  'delete':
                  (item)=>(
                    <td>
                      <CButton className="btn btn-danger" onClick={(e)=> {deleteUser(item._id);}}>Delete</CButton>                 
                    </td>
                  ),

              }}
            />
}
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
        {/* <CButton className="btn btn-primary float-right" onClick={addButton}>
          Add
        </CButton> */}
      </div>
    </>
  )
}

export default UserList
